	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Novell Acquires SUSE LINUX","date":"04 Nov 2003","slug":"/2003-11-04-novell-acquires-suse-linux/","tags":"Y2003"},"html":"<p><a title=\"$210 Million\" href=\"http://www.suse.com/us/company/press/press_releases/archive03/novell_suse.html\">Novell Acquires SUSE LINUX</a></p>"},"next":{"fields":{"title":"Should I be worried?","date":"03 Nov 2003","slug":"/2003-11-03-should-i-be-worried/"}},"previous":{"fields":{"title":"PHP 4.3.4 Released","date":"04 Nov 2003","slug":"/2003-11-04-php-434-released/"}}},{"node":{"fields":{"title":"Should I be worried?","date":"03 Nov 2003","slug":"/2003-11-03-should-i-be-worried/","tags":"Y2003"},"html":"<p>My wife will be attending a costume party on a <a title=\"Everything about the Maltese Islands - Malta, Gozo and Comino\" href=\"http://www.visitmalta.com/\">Mediterranean island</a> this week, put on by her employer. <a title=\"Calpundit: Absolutely, Your Honor, It Was a Legitimate Business Expense\" href=\"http://www.calpundit.com/archives/002551.html\">Should I be worried?</a></p>\n<p>At least her party has a pirate theme instead of <a title=\"MSNBC: It?s My Party ...\" href=\"http://www.msnbc.com/news/987944.asp?0cv=KB10&cp1=1\">ancient Rome</a>.</p>"},"next":{"fields":{"title":"Ch-ch-ch-changes","date":"01 Nov 2003","slug":"/2003-11-01-ch-ch-ch-changes/"}},"previous":{"fields":{"title":"Novell Acquires SUSE LINUX","date":"04 Nov 2003","slug":"/2003-11-04-novell-acquires-suse-linux/"}}},{"node":{"fields":{"title":"Ch-ch-ch-changes","date":"01 Nov 2003","slug":"/2003-11-01-ch-ch-ch-changes/","tags":"Y2003"},"html":"<p>The German journalist and blogger <a title=\"Chemie und Wahnsinn: WorldWideKlein\" href=\"http://www.worldwideklein.com/orange/P909/\">WorldWideKlein</a> on leaving Washington after 7 years (translation mine):</p>\n<p>\"The USA to which I came in July 1996 was a completely different country than the USA that I am now leaving: well-off, self-satisfied, open, optimistic. It was the land of Bill Clinton and the Internet, sudden wealth, free of worries. Today on the other hand, my adopted countrymen find themselves being driven from one fear to the next (and many of them let themselves be driven), threatend, despised, isolated. Who is responsible for this change -  everyone has his own their theory, I'm sure. I've been so close to the action that right now I don't have an overview, I'm missing the big picture. But I already noticed in  1996/1997 how helpful it sometimes is to leave a country and to observe it from the outside.\"</p>\n<p>Welcome home, and keep on blogging!</p>"},"next":{"fields":{"title":"Not-so-latent Antisemitism","date":"31 Oct 2003","slug":"/2003-10-31-not-so-latent-antisemitism/"}},"previous":{"fields":{"title":"Should I be worried?","date":"03 Nov 2003","slug":"/2003-11-03-should-i-be-worried/"}}}],"pathPrefix":"","first":false,"last":false,"index":596,"pageCount":1101}}

